import React from "react"

import PropTypes from "prop-types"


class Pagination extends React.Component{
  constructor(props) {
    super(props);

    this.state = {
      total: props.total,
      perPage: props.perPage,
      items: [... Array(Math.round(props.total / props.perPage)).keys()],
      active: 2
    }
  }

  handlePageClick = (index, e) => {
    this.setState({
      active: index
    });
  }

  handleArrowClick = (e) => {
    let direction = e.target.attributes.direction.value;
    this.setState((prevState, props) => {
      return {
        active: direction === "next" ? prevState.active + 1 : prevState.active - 1
      }
    })
  }

  render(){
    return(
      <ul className="pagination">
        <li className="page-item arrow">
          <span className={"page-link " + (this.state.active === 0 ? "disabled" : "")}
             onClick={this.handleArrowClick}
             direction="prev"
          >Previous</span>
        </li>
        {
          this.state.items.map((item, index) => {
            return(
              <li className={"page-item " + (this.state.active === index ? "active" : "")} key={"pagination-" + index}>
                <span className="page-link" onClick={this.handlePageClick.bind(this, index)}>
                  {index + 1}
                </span>
              </li>
            )
          })
        }
        <li className="page-item arrow">
          <span className={"page-link " + (this.state.active === this.state.items.length - 1 ? "disabled" : "")}
             onClick={this.handleArrowClick}
             direction="next"
          >Next</span>
        </li>
      </ul>
    )
  }
}

Pagination.propTypes = {
  total: PropTypes.number,
  perPage: PropTypes.number
}

export default Pagination