import React from "react";
import SyntaxHighlighter from 'react-syntax-highlighter';
import { docco } from 'react-syntax-highlighter/dist/esm/styles/hljs';

import Pagination from '../components/ui/Pagination';
import PaginationIcon from '../styles/img/svg/pagination.svg';

import Layout from "../components/layout"
import SEO from "../components/seo"

const PaginationPage = () => (
  <Layout>
    <SEO title="Pagination" />
    <div className="container">
      <div className="island px-4 py-3 mb-3">
        <h2>
          <PaginationIcon className="mr-2" />
          Pagination
        </h2>
        <div className="mt-3">
          <Pagination total={15} perPage={3} />
        </div>
        <div className="mt-3">
          <SyntaxHighlighter language="html" style={docco} className="rounded" showLineNumbers="true">
{`<ul class="pagination">
    <li class="page-item arrow"><span class="page-link" direction="prev">Previous</span></li>
    <li class="page-item"><span class="page-link">1</span></li>
    <li class="page-item active"><span class="page-link">2</span></li>
    <li class="page-item"><span class="page-link">3</span></li>
    <li class="page-item"><span class="page-link">4</span></li>
    <li class="page-item arrow"><span class="page-link" direction="next">Next</span></li>
</ul>`}
          </SyntaxHighlighter>
        </div>
      </div>
    </div>
  </Layout>
)

export default PaginationPage